	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"https://github.com/AntCas/OpenPanoThermo\">https://github.com/AntCas/OpenPanoThermo</a></p>\n<p><img src=\"/images/hcil/thermo.jpg\" alt=\"alt text\" title=\"un-normalized thermographic panorama\">\n<img src=\"/images/hcil/thermo-normalized.jpg\" alt=\"alt text\" title=\"normalized thermographic panorama\"></p>\n<p>Project thermo reliably creates thermographic panoramas of indoor locations in order to identify energy inefficiencies in rooms and buildings.</p>\n<p>I worked on this project as a volunteer at the Human Computer Interaction lab at the University of Maryland, College Park during my last semester there as a Computer Science senior.</p>\n<p>The software I wrote was integrated into a self-contained device which could take a series of thermographic images over the course of several days in order to map heat issues over time.</p>\n<p>The project is a fork of <a href=\"https://github.com/ppwwyyxx/OpenPano\">OpenPano</a> adapted for thermographic images.</p>\n<p>After many dead-ends I ended up achieving great results by first normalizing and enhancing the images using <a href=\"https://www.imagemagick.org/script/index.php\">ImageMagick</a>, then stitching them together using OpenPano.</p>\n<blockquote>\n<p><img src=\"/images/hcil/window0.png\" alt=\"alt text\" title=\"thermographic image of a window\">\n<img src=\"/images/hcil/window1.png\" alt=\"alt text\" title=\"thermographic image of a window\">\n<img src=\"/images/hcil/window2.png\" alt=\"alt text\" title=\"thermographic image of a window\">\n<img src=\"/images/hcil/window3.png\" alt=\"alt text\" title=\"thermographic image of a window\"></p>\n</blockquote>","frontmatter":{"date":"January 01, 2017","path":"/hcil","title":"HCIL - Project Thermo"}}},"pathContext":{}}

